.content {
  display: flex;
  border-radius: 50%;
  background: linear-gradient(to right, rgba(128, 26, 178, 1), rgba(76, 76, 255, 1));
  padding: 2px;
  position: relative;
  margin-left: -20px;
}

.content:first-child {
  margin-left: 0;
}

.item {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  box-sizing: border-box;
  background: #131316;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
	
}

.image {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.hide {
  opacity: .6;
}